@import url('https://fonts.googleapis.com/css2?family=Alice&family=Rubik:wght@300;400&display=swap');

body {
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  font-size: 16px;
}

.title-font {
  font-family: 'Alice', serif;
}

h2 {
  border-bottom: 8px solid #2b2a23;
  display:table;
}

.yellow-title {
  color: #f7f47c;
  border-bottom: 8px solid #f7f47c;
}

.light-font {
  font-weight: 300;
}

.bold-font {
  font-weight: 400;
}

 h1 {
   display:table-cell;
 }
.name {
  font-size: 2.5em;
}

.intro-border {
  border-bottom: 16px solid #2b2a23;
}

.header-section {
  background-color: red;
  min-height: 100vh;
}

.header-graphic-01 {
  position: absolute;
  top: 0;
}

.header-graphic-02 {
  position: absolute;
  bottom: 0;
}

.menu {
  list-style-type: none;
  border-left: 8px solid #2b2a23;
  margin: 0;
  padding-left: 20px;
  line-height: 2em;
  
}

.menu li a {
  font-weight: 400;
  color: #2b2a23;
}

.menu li a:hover {
  color: #191814;
  text-decoration: none;
}

.yellow_bgrd {
  background-color: #f7f47c;
}

.bgrd_shaded {
  background-color: #e5e4d3;
}

.bgrd_dark {
  background-color: #2b2a23;
}

